/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  font-family: inherit;
  vertical-align: baseline;
}

/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

html {
  font-size: 62.5%;
}

body,
input,
textarea,
button {
  font-size: 1.4rem;
  color: #fa9a9c;
  font-family: "Titan One", sans-serif;
  font: normal 400 1.4rem "Titan One", sans-serif;
  font-weight: 400;
  font-style: normal;
}

.main-container {
  display: grid;
  grid-template-rows: 100vh;
  position: relative;
  background-color: #fde8e7;
  background: linear-gradient(0deg, #fde8e7 0%, #d6797a 100%);
}
.main-container .error-container {
  width: 100%;
  position: absolute;
  align-self: center;
  transition: opacity 0.7s;
  opacity: 0;
  z-index: 0;
  display: flex;
  align-items: center;
  justify-content: center;
}
.main-container .error-container.show-error {
  z-index: 10;
  opacity: 1;
}
.main-container .error-container video {
  max-width: 100%;
}
.main-container .success-container {
  width: 100%;
  position: absolute;
  align-self: center;
  z-index: 100;
  opacity: 1;
  display: flex;
  align-items: center;
  justify-content: center;
}
.main-container .success-container img {
  position: absolute;
  z-index: 101;
  height: 100vh;
}
.main-container .success-container video {
  max-width: 100%;
}
.main-container .question-container {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  position: relative;
  z-index: 1;
}
.main-container .question-container .heading {
  font-size: 4rem;
  margin-bottom: 2rem;
  text-align: center;
  -webkit-text-stroke: 0.2rem #faf6f3;
  text-shadow: 0 0.4rem #d6797a;
}
.main-container .question-container .buttons {
  display: flex;
  gap: 2rem;
}
.main-container .question-container .buttons button {
  background-color: #fa9a9c;
  height: 8rem;
  width: 8rem;
  border-radius: 100%;
  box-shadow: 0 0.6rem #d6797a;
  border: 0.5rem solid #faf6f3;
  color: #faf6f3;
  font-size: 2.4rem;
  position: relative;
  transition: all 0.2s;
  cursor: pointer;
  top: 0;
}
.main-container .question-container .buttons button:hover {
  top: 0.2rem;
  box-shadow: 0 0.3rem #d6797a;
}
.main-container .question-container .buttons button:active {
  top: 0.2rem;
  box-shadow: 0 0 #d6797a;
}